import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

// 
const state = {
    topType:'search',
    topStep:0,
    shopCart:{
        total:1
    }
}

//  mutations
const mutations = {
    //
    SETHAEDTYPE(state,type){
        state.topType=type
    },
    
}


//  store 
export default new Vuex.Store({
    state,
    mutations
})


